'use strict';

var assign = require('lodash/assign');

var Class = require('./class');

var callbackToPromise = require('./callback_to_promise');

var Record = Class.extend({
  init: function init(table, recordId, recordJson) {
    this._table = table;
    this.id = recordId || recordJson.id;
    this.setRawJson(recordJson);
    this.save = callbackToPromise(this.save, this);
    this.patchUpdate = callbackToPromise(this.patchUpdate, this);
    this.putUpdate = callbackToPromise(this.putUpdate, this);
    this.destroy = callbackToPromise(this.destroy, this);
    this.fetch = callbackToPromise(this.fetch, this);
    this.updateFields = this.patchUpdate;
    this.replaceFields = this.putUpdate;
  },
  getId: function getId() {
    return this.id;
  },
  get: function get(columnName) {
    return this.fields[columnName];
  },
  set: function set(columnName, columnValue) {
    this.fields[columnName] = columnValue;
  },
  save: function save(done) {
    this.putUpdate(this.fields, done);
  },
  patchUpdate: function patchUpdate(cellValuesByName, opts, done) {
    var that = this;

    if (!done) {
      done = opts;
      opts = {};
    }

    var updateBody = assign({
      fields: cellValuesByName
    }, opts);

    this._table._base.runAction('patch', '/' + this._table._urlEncodedNameOrId() + '/' + this.id, {}, updateBody, function (err, response, results) {
      if (err) {
        done(err);
        return;
      }

      that.setRawJson(results);
      done(null, that);
    });
  },
  putUpdate: function putUpdate(cellValuesByName, opts, done) {
    var that = this;

    if (!done) {
      done = opts;
      opts = {};
    }

    var updateBody = assign({
      fields: cellValuesByName
    }, opts);

    this._table._base.runAction('put', '/' + this._table._urlEncodedNameOrId() + '/' + this.id, {}, updateBody, function (err, response, results) {
      if (err) {
        done(err);
        return;
      }

      that.setRawJson(results);
      done(null, that);
    });
  },
  destroy: function destroy(done) {
    var that = this;

    this._table._base.runAction('delete', '/' + this._table._urlEncodedNameOrId() + '/' + this.id, {}, null, function (err, response, results) {
      if (err) {
        done(err);
        return;
      }

      done(null, that);
    });
  },
  fetch: function fetch(done) {
    var that = this;

    this._table._base.runAction('get', '/' + this._table._urlEncodedNameOrId() + '/' + this.id, {}, null, function (err, response, results) {
      if (err) {
        done(err);
        return;
      }

      that.setRawJson(results);
      done(null, that);
    });
  },
  setRawJson: function setRawJson(rawJson) {
    this._rawJson = rawJson;
    this.fields = this._rawJson && this._rawJson.fields || {};
  }
});
module.exports = Record;